<template>
	<v-form v-model="valid" ref="form">
		<div class="title mb-3">
			Data Prestasi
		</div>
		
		<v-select
			outlined
			dense
			label="Tingkat"
			v-model="tingkat"
			:items="dataTingkat"
			item-text="nama"
			item-value="nama"
		></v-select>
		<v-autocomplete
			outlined
			dense
			label="Bidang"
			:disabled="tingkat===''?true:false"
			v-model="dataPrestasi.bidang_id"
			:items="detailBidang"
			item-text="nama"
			item-value="id"
		></v-autocomplete>
		<v-textarea
			outlined
			dense
			label="Keterangan"
			v-model="dataPrestasi.keterangan"
			required
		></v-textarea>
		<div class="subtitle-1">Relevansi</div>
		<v-radio-group 
			row 
			v-model="relevan" 
			class="mt-0 d-flex"
      validate-on-blur>
			<v-radio
				label="Ya"
				:value="'true'"
			></v-radio>
			<v-radio
				label="Tidak"
				:value="'false'"
			></v-radio>
		</v-radio-group>
		<div class="subtitle-1">Mempunyai Portofolio</div>
		<v-radio-group 
			row 
			v-model="portfolio" 
			class="mt-0 d-flex"
      validate-on-blur>
			<v-radio
				label="Ya"
				:value="'true'"
			></v-radio>
			<v-radio
				label="Tidak"
				:value="'false'"
			></v-radio>
		</v-radio-group>

		<div class="d-flex">
			<v-spacer></v-spacer>
			<v-btn
				text
				class="mr-3"
				@click="$emit('step', 2)"
				color="primary">
				kembali
			</v-btn>
			<v-btn
				depressed
				type="submit"
				@click.prevent="validate"
				color="primary">
				selanjutnya
			</v-btn>
		</div>
	</v-form>
</template>

<script>
	import { mapState } from 'vuex'

	export default{
		data: () => ({
			tingkat: '',
			dataPrestasi: {
				bidang_id: '',
				// tingkat: '',
				relevan: null,
				portfolio: null,
				keterangan: ''
			},
			relevan: null,
			portfolio: null,
			valid: true,
			requiredRule: [v => !!v || 'Data harus diisi']
		}),

		computed: {
			...mapState('dataAdmin', ['dataBidang', 'dataTingkat']),
			detailBidang() {
				return this.dataBidang.filter(bidang => bidang.tingkat === this.tingkat)
			}
		},

		methods: {
			validate() {
				if (this.$refs.form.validate()) {
					if (this.relevan === 'true') {
						this.dataPrestasi.relevan = true
					} else if (this.relevan === 'false') {
						this.dataPrestasi.relevan = false
					}
					if (this.portfolio === 'true') {
						this.dataPrestasi.portfolio = true
					} else if (this.portfolio === 'false') {
						this.dataPrestasi.portfolio = false
					}
					this.$store.commit('dataPeserta/updateDataPrestasi', this.dataPrestasi)
					this.$emit('step', 4)
				}
			}
		}
	}
</script>