<template>
	<v-form v-model="valid" ref="form">
		<div class="title mb-3">
			Data Personal
		</div>
		<v-text-field
			label="Nama lengkap"
			v-model="dataPersonal.nama"
			outlined
			dense
			required
			:rules="nameRule"
		></v-text-field>
		<v-text-field
			label="E-mail"
			v-model="dataPersonal.email"
			outlined
			dense
			required
			:rules="emailRule"
		></v-text-field>
		<v-text-field
			label="No. Hp/WA"
			v-model="dataPersonal.nohp"
			outlined
			dense
			required
			:rules="requiredRule"
		></v-text-field>
		<div class="subtitle-1">Jenis kelamin</div>
		<v-radio-group 
			row 
			v-model="dataPersonal.jenis_kelamin" 
			class="mt-0 d-flex"
      :rules="requiredRule"
      validate-on-blur>
			<v-radio
				label="Laki-laki"
				value="L"
			></v-radio>
			<v-radio
				label="Perempuan"
				value="P"
			></v-radio>
		</v-radio-group>
		<div class="subtitle-1">Status Siswa</div>
		<v-radio-group 
			row 
			v-model="siswa_pge" 
			class="mt-0 d-flex"
      :rules="requiredRule"
      validate-on-blur>
			<v-radio
				label="Ya"
				:value="'true'"
			></v-radio>
			<v-radio
				label="Tidak"
				:value="'false'"
			></v-radio>
		</v-radio-group>
		<div class="d-flex">
			<v-spacer></v-spacer>
			<v-btn
				depressed
				type="submit"
				@click.prevent="validate"
				color="primary">
				selanjutnya
			</v-btn>
		</div>
	</v-form>
</template>

<script>
	export default{
		data: () => ({
			dataPersonal: {
				nama: '',
				email: '',
				nohp: '',
				jenis_kelamin: '',
				siswa_pge: null,
				aktif: true
			},
			siswa_pge: null,
			valid: true,
			requiredRule: [v => !!v || 'Data harus diisi']
		}),

		computed: {
			emailRule () {
        return [
          ...this.requiredRule,
          v => /.+@.+\..+/.test(v) || 'Data berupa e-mail'
        ]
      },
      nameRule () {
        return [
          ...this.requiredRule,
          v => (v && v.length >= 6) || 'Nama lebih dari 6 karakter',
        ]
      },
		},

		methods: {
			validate() {
				if (this.$refs.form.validate()) {
					if (this.siswa_pge === 'true') {
						this.dataPersonal.siswa_pge = true
					} else {
						this.dataPersonal.siswa_pge = false						
					}
					this.$store.commit('dataPeserta/updateDataPersonal', this.dataPersonal)
					this.$emit('step', 2)
				}
			}
		}
	}
</script>