<template>
	<v-form v-model="valid" ref="form">
		<div class="title mb-3">
			Data Raport
		</div>
		
		<v-tabs v-model="tab">
			<v-tabs-slider></v-tabs-slider>
			<v-tab
				v-for="(item, i) in newDataKelas" :key="i"
				:href="`#tab-${i}`"
				active-class="outlined-tab">
				Kelas
				{{ item.nama }}
			</v-tab>
			<v-tab-item 
				:value="`tab-${i}`"
				v-for="(kelas, i) in newDataKelas" :key="kelas.id">
				<v-row>
					<template v-for="(semester, indexSemester) in tampilSemester(i)">
						<v-col
							cols="12" md="6" sm="12" 
							:key="indexSemester">
							<div class="subtitle-1 font-weight-bold">
								Semester {{semester[indexSemester].semester}}
							</div>
							<v-simple-table 
								dense class="my-3 table-raport outlined-tab">
								<thead>
									<tr>
										<td rowspan="2">Mata Pelajaran</td>
										<td rowspan="2">KKM</td>
										<td colspan="2" class="text-center">
											Nilai Kognitif
										</td>
									</tr>
									<tr>
										<td>Skala 4</td>
										<td>Skala 100</td>
									</tr>
								</thead>
								<tbody>
									<tr 
										v-for="(item, i) in semester" 
										:key="i">
										<td>{{ namaPelajaran(item.pelajaran_id) }}</td>
										<td>
											<v-text-field
												v-model="item.kkm"
												type="number"
												required
												:rules="kkmRule"
												outlined
												dense
												autocomplete="off"
											></v-text-field>
										</td>
										<td>
											<v-text-field
												v-model="item.nilai_4"
												type="number"
												:rules="empatRule"
												outlined
												dense
												autocomplete="off"
											></v-text-field>
										</td>
										<td>
											<v-text-field
												v-model="item.nilai"
												type="number"
												:rules="seratusRule"
												outlined
												dense
												autocomplete="off"
											></v-text-field>
										</td>
									</tr>
								</tbody>
							</v-simple-table>
						</v-col>
					</template>
				</v-row>
      </v-tab-item>
		</v-tabs>

		<div class="d-flex">
			<v-spacer></v-spacer>
			<v-btn
				text
				@click="$emit('step', 3)"
				class="mr-3"
				color="primary">
				kembali
			</v-btn>
			<v-btn
				depressed
				type="submit"
				@click.prevent="validate"
				color="primary">
				selanjutnya
			</v-btn>
		</div>
	</v-form>
</template>

<style>
	.outlined-tab{ border: 1px solid #48AEF0; }
	.v-tabs-slider{ width: 0;height: 0; }
	.table-raport .v-text-field__details{ display: none; }
	.table-raport.v-data-table tbody td{ padding: 8px 16px !important; }
</style>

<script>
	import { mapState } from 'vuex'
	import { findIndex, uniqBy } from 'lodash'

	export default{
		data: () => ({
			valid: true,
			numberRule: [v => v >= 0 || 'Lebih dari 0'],
			requiredRule: [v => !!v || 'Harus diisi'],
			tab: null
		}),

		computed: {
			...mapState('dataAdmin', ['dataKelas', 'dataPelajaran']),
			...mapState('dataPeserta', ['dataSekolah']),
			seratusRule() {
				return [
					...this.numberRule,
					v => v <= 100 || 'Kurang dari 100'
				]
			},
			empatRule() {
				return [
					...this.numberRule,
					v => v <= 4 || 'Kurang dari 4'
				]
			},
			kkmRule() {
				return [
					...this.numberRule,
					...this.requiredRule,
					v => v <= 100 || 'Kurang dari 100'
				]
			},
			newDataKelas() {
				let kelas = []
				if (this.dataSekolah.semester <= 2) {
					kelas = ['X']
				} else if (this.dataSekolah.semester > 2 && this.dataSekolah.semester <= 4) {
					kelas = ['X', 'XI']
				} else if (this.dataSekolah.semester > 4) {
					kelas = ['X', 'XI', 'XII']
				}
				return this.dataKelas.filter((item, i) => {
					if (item.nama === kelas[i]) return item
				})
			},
			dataRaport() {
				const semester = this.dataSekolah.semester
				let dataRaport = []
				for(let semesterKe = 0;semesterKe < semester;semesterKe++) {
					let namaKelas = ''
					if (semesterKe < 2) {
						namaKelas = 'X'
					} else if (semesterKe > 1 && semesterKe < 4) {
						namaKelas = 'XI'
					} else {
						namaKelas = 'XII'
					}
					dataRaport[semesterKe] = this.newDataKelas
						.filter(kelas => kelas.nama === namaKelas)
						.map(kelas => {							
							return this.dataPelajaran
								.filter(pelajaran => findIndex(pelajaran.jenjang, { nama: this.dataSekolah.jenjang }) !== -1 && pelajaran.kelas.id === kelas.id && pelajaran.jurusan.id === this.dataSekolah.jurusan_id && pelajaran.kurikulum.id === this.dataSekolah.kurikulum_id)
								.map(pelajaran => {
									return {
										kelas_id: pelajaran.kelas.id,
										pelajaran_id: pelajaran.id,
										semester: semesterKe+1,
										kkm: '',
										nilai_4: '',
										nilai: ''
									}
								}).flat()
						}).flat()
				}

				return dataRaport
			}		
		},

		methods: {
			tampilSemester(val) {
				return this.dataRaport.slice(val*2, 2+(val*2))
			},
			namaPelajaran(val) {
				const hasil = this.dataPelajaran
					.filter(data => data.id === val)
				return {...hasil[0]}.nama			
			},			
			validate() {
				if (this.$refs.form.validate()) {
					this.$store.commit('dataPeserta/updateDataRapor', this.dataRaport)
					this.$emit('step', 5)
				}
			}
		}
	}
</script>