<template>
	<div>
		<v-row>
			<v-col cols="12" md="4" sm="12" class="pt-0">
				<div class="title mb-3">
					Data Primer
				</div>

				<v-list dense>
					<template v-for="(item, i) in data">
						<v-list-item :key="i">
	            <v-list-item-content>
	              <v-list-item-title class="subtitle-1">
	              	{{ item.text }}
	              </v-list-item-title>
	            </v-list-item-content>
	            <v-list-item-icon>
	              <v-icon color="success" v-if="item.data !== ''">
	              	mdi-checkbox-marked-outline
	              </v-icon>
	              <v-icon color="red" v-else>mdi-close-box-outline</v-icon>
	            </v-list-item-icon>
	          </v-list-item>
					</template>
				</v-list>
			</v-col>
			<v-col cols="12" md="8" sm="12" class="pt-0">
				<div class="title mb-3">
					Data PTN/Prodi
				</div>

				<v-simple-table>
					<tbody>
						<tr v-for="(item, i) in detailPtn" :key="i">
							<td>Pilihan-{{ i+1 }}</td>
							<td class="text-uppercase">{{ item.nama }}</td>
							<td class="text-uppercase">{{ item.prodi.nama }}</td>
						</tr>
					</tbody>
				</v-simple-table>

				<Notif :msg="msg" :status="status" @visible="visible" />
			</v-col>
		</v-row>
		
		<div class="d-flex">
			<v-spacer></v-spacer>
			<v-btn
				text
				class="mr-3"
				@click="$emit('step', 5)"
				color="primary">
				kembali
			</v-btn>
			<v-btn
				depressed
				@click="tambahPeserta"
				:loading="isLoading"
				color="primary">
				selesai
			</v-btn>
		</div>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Notif from '@/components/Notif'

	export default{
		data: () => ({
			msg: {
				success: 'Data berhasil ditambahkan',
				error: 'Data gagal ditambahkan',
				visible: false
			}
		}),

		components: { Notif },

		computed: {
			...mapState('dataPeserta', ['dataPersonal', 'dataSekolah', 'dataPrestasi', 'dataRapor', 'dataPilihanPtn', 'isLoading', 'status']),
			...mapState('dataPtn', ['detailPtn']),
			data() {
				let prestasi = ''
				if (this.dataPrestasi.bidang_id === '') {
					prestasi = ''
				} else {
					prestasi = this.dataPrestasi
				}
				return [
					{ text: 'Data Personal', data: this.dataPersonal },
					{ text: 'Data Sekolah', data: this.dataSekolah },
					{ text: 'Data Prestasi', data: prestasi },
					{ text: 'Data Raport', data: this.dataRapor }
				]
			}
		},

		methods: {
			tambahPeserta() {
				delete this.dataSekolah.jenjang
				delete this.dataSekolah.kurikulum_id
				const dataPeserta = {
					personal: this.dataPersonal,
					sekolah: this.dataSekolah,
					prestasi: this.dataPrestasi,
					ptn: this.dataPilihanPtn,
					rapor: this.dataRapor
				}
				if (this.dataPrestasi.bidang_id === '') delete dataPeserta.prestasi
				this.$store.dispatch('dataPeserta/postDataPeserta', dataPeserta)
				this.$store.dispatch('dataPeserta/updateIsLoading', true)
				this.msg.visible = true
			},
			visible(val) {
				this.msg.visible = val
				this.$store.commit('dataPeserta/updateStatus', null)
			}
		}
	}
</script>