<template>
  <div>
    <v-card-title class="py-2">
      <v-icon class="mr-3">mdi-face</v-icon>
      Daftar Peserta
    </v-card-title>
    <v-divider></v-divider>
    <v-card-text class="pa-0">
      <v-tabs>
        <v-tab v-for="(item, i) in tabs" :key="i">
          <v-icon class="mr-2" size="30" :color="item.color">{{ item.icon }}</v-icon>
          <div class="text-capitalize text-left">
            <div>{{ item.title }}</div>
            <div class="body-2">{{ item.subtitle }}</div>
          </div>
        </v-tab>
      </v-tabs>
    </v-card-text>
    <v-divider></v-divider>
    <v-card-text class="pa-0">
      <v-stepper v-model="el" class="elevation-0">
        <v-stepper-header class="elevation-0">
          <template v-for="(item, index) in steps">
            <v-stepper-step 
              :key="index" 
              :complete="el > item.step" 
              :step="item.step">
              {{ item.text }}
            </v-stepper-step>

            <v-divider v-if="index !== steps.length-1"></v-divider>
          </template>
        </v-stepper-header>

        <v-divider></v-divider>

        <v-stepper-items>
          <template v-for="(item, index) in steps">
            <v-stepper-content :key="index" :step="item.step">
              <component :is="item.text" @step="step"></component>
            </v-stepper-content>
          </template>
        </v-stepper-items>
      </v-stepper>
    </v-card-text>
  </div>
</template>

<script>
	import Personal from './Register/Personal'
  import Sekolah from './Register/Sekolah'
  import Prestasi from './Register/Prestasi'
  import Raport from './Register/Raport'
  import PTN from './Register/ptn'
  import Validasi from './Register/Validasi'

  export default {
    data () {
      return {
        el: 1,
        steps: [
        	{ text: 'Personal', step: 1 },
        	{ text: 'Sekolah', step: 2 },
        	{ text: 'Prestasi', step: 3 },
        	{ text: 'Raport', step: 4 },
        	{ text: 'PTN', step: 5 },
        	{ text: 'Validasi', step: 6 }
        ],
        tabs: [
          { title: 'Register Siswa', subtitle: 'Input Data Siswa', icon: 'mdi-account', link: '', color: 'success' },
          { title: 'Data Primer', subtitle: 'input data primer', icon: 'mdi-database', link: '', color: 'red' },
          { title: 'Analisis', subtitle: 'cek analisis ITSS', icon: 'mdi-database', link: '', color: 'orange' },
          { title: 'Peringkat', subtitle: 'cek peringkat ITSS', icon: 'mdi-database', link: '', color: 'blue lighten-2' }
        ]
      }
    },

    components: {
      Validasi,
      PTN,
      Raport,
      Prestasi,
      Sekolah,
    	Personal
    },

    created() {
      this.$store.dispatch('dataTempat/getDataProv')
      this.$store.dispatch('dataAdmin/getDataJurusan')
      this.$store.dispatch('dataAdmin/getDataKelas')
      this.$store.dispatch('dataAdmin/getDataBidang')
      this.$store.dispatch('dataAdmin/getDataTingkat')
      this.$store.dispatch('dataAdmin/getDataPelajaran')
      this.$store.dispatch('dataAdmin/getDataJenisKelas')
      this.$store.dispatch('dataAdmin/getDataJenjang')
      this.$store.dispatch('dataAdmin/getDataKurikulum')
      this.$store.dispatch('dataPtn/getDataPtnByName')
    },

    methods: {
      step (val) {
        this.el = val
      }
    }
  }
</script>