<template>
	<v-form v-model="valid" ref="form">
		<div class="title mb-3">
			Data Sekolah
		</div>
		
		<v-row>
			<v-col cols="12" md="6" sm="12">
				<v-autocomplete
					outlined
					dense
					label="Provinsi"
					v-model="provinsi_id"
					:items="dataProv"
					item-text="nama"
					item-value="id"
					required
					:rules="requiredRule"
					hide-selected
          hide-no-data
				></v-autocomplete>
				<v-autocomplete
					outlined
					dense
					label="Kabupaten"
					v-model="kabupaten_id"
					:items="dataKab"
					item-text="nama"
					item-value="id"
					:disabled="provinsi_id===''?true:false"
					required
					:rules="requiredRule"
					hide-selected
          hide-no-data
				></v-autocomplete>
				<v-select
					dense
					outlined
					label="Jenis Sekolah"
					v-model="jenis_sekolah"
					:items="dataJenjang"
					item-text="nama"					
					item-value="nama"					
					:disabled="kabupaten_id===''?true:false"
					required
					:rules="requiredRule"
				></v-select>
				<v-autocomplete
					outlined
					dense
					label="Nama Sekolah"
					v-model="sekolah_id"
					:items="detailSekolah"
					item-text="sekolah"
					return-object
					:disabled="jenis_sekolah===''?true:false"
					required
					:rules="requiredRule"
					hide-selected
          hide-no-data
				></v-autocomplete>
			</v-col>
			<v-col cols="12" md="6" sm="12">
				<v-autocomplete
					:disabled="sekolah_id===''?true:false"
					outlined
					dense
					label="Jurusan"
					v-model="data.jurusan_id"
					:items="detailJurusan"
					item-text="nama"
					item-value="id"
					required
					:rules="requiredRule"
					hide-selected
          hide-no-data
				></v-autocomplete>
				<v-select
					outlined
					dense
					label="Kelas"
					v-model="kelas"
					:items="dataKelas"
					item-text="nama"
					return-object
					required
					:rules="requiredRule"
					hide-selected
          hide-no-data
				></v-select>
				<v-select
					dense
					outlined
					:rules="requiredRule"
					label="Jenis Kelas"
					:items="dataJenisKelas"
					item-text="nama"
					item-value="id"
					v-model="data.jenis_kelas_id"
				></v-select>
				<v-select
					outlined
					dense
					label="Semester Terakhir"
					v-model="data.semester"
					:items="semester"
					required
					:rules="requiredRule"
					hide-selected
          hide-no-data
				></v-select>
			</v-col>
		</v-row>

		<div class="d-flex">
			<v-spacer></v-spacer>
			<v-btn
				text
				class="mr-3"
				@click="$emit('step', 1)"
				color="primary">
				kembali
			</v-btn>
			<v-btn
				depressed
				type="submit"
				@click.prevent="validate"
				color="primary">
				selanjutnya
			</v-btn>
		</div>
	</v-form>
</template>

<script>
	import { mapState } from 'vuex'

	export default{
		data: () => ({
			data: {
				sekolah_id: '',
        jurusan_id: '',
        kelas_id: 0,
        jenis_kelas_id: '',
        semester: 0,
        jenjang: ''
			},
			detailSekolah: [],
			provinsi_id: '',
			kabupaten_id: '',
			jenis_sekolah: '',
			valid: true,
			kelas: '',
			sekolah_id: '',
			// jenis: ['MA', 'SMA', 'SMK'],
			semester: [],
			requiredRule: [v => !!v || 'Data harus diisi']
		}),		

		computed: {
			...mapState('dataTempat', ['dataProv', 'dataKab']),
			...mapState('dataAdmin', ['dataJurusan', 'dataKelas', 'dataSekolah', 'dataJenisKelas', 'dataJenjang']),
			detailJurusan: {
				get() {
					return this.dataJurusan.filter(jurusan => {
						let jenjang = jurusan.jenjang.indexOf(this.jenis_sekolah)
						if (jenjang !== -1) return jurusan
					})
				}
			}
		},

		watch: {
			'sekolah_id': function (val) {
				this.data.sekolah_id = val.id						
				this.data.kurikulum_id = val.kurikulum.id						
			},
			provinsi_id(val) {
				this.$store.dispatch('dataTempat/getDataKab', val)						
			},
			kabupaten_id(val) {	
				this.$store.dispatch('dataAdmin/getDataSekolah', {
      		provinsi_id: this.provinsi_id,
      		kabupaten_id: this.kabupaten_id
      	})
			},
			jenis_sekolah(val) {
      	this.detailSekolah = this.dataSekolah
      		.filter(sekolah => sekolah.jenjang.nama === val)
				this.data.jenjang = val			
			},
			kelas(val) {
				this.data.kelas_id = val.id
				if (val.nama === 'X') {
					this.semester = [1,2]
				} else if (val.nama === 'XI') {
					this.semester = [1,2,3,4]
				} else {
					this.semester = [1,2,3,4,5]
				}
			}
		},

		methods: {
			validate() {
				if (this.$refs.form.validate()) {
					this.$store.commit('dataPeserta/updateDataSekolah', this.data)
					this.$emit('step', 3)
				}
			}
		}
	}
</script>